div.palette-demo {
  position: relative;
  display: flex;
  padding: 8px;
  block-size: 72px;
  inline-size: 216px;
}
div.palette-demo span {
  position: relative;
  flex: 1 1 0;
  flex-direction: column;
  display: flex;
}
div.palette-demo .accent span {
  position: relative;
  flex: 1 1 0;
  flex-direction: row;
  inline-size: 36px;
}

.palette-demo--blue-gold .brand-primary {
  background-color: #138aab;
}
.palette-demo--blue-gold .brand-secondary {
  background-color: #e6ab49;
}
.palette-demo--blue-gold .brand-primary-accent {
  background-color: #159ec4;
}
.palette-demo--blue-gold .brand-secondary-accent {
  background-color: #f1b85a;
}

.palette-demo--red-bone .brand-primary {
  background-color: #ac143a;
}
.palette-demo--red-bone .brand-secondary {
  background-color: #d9d4c2;
}
.palette-demo--red-bone .brand-primary-accent {
  background-color: #9a1134;
}
.palette-demo--red-bone .brand-secondary-accent {
  background-color: #c9c2a8;
}